@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Montserrat';
}

.App h1 {
  font-family: 'Nunito';
}

.app-nav {
  width: 50vw;
  margin-left: 50vw;
  height: 75px;
  font-size: 2vh;
}

.nav-list {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 2vh;
}

.nav-item {
  list-style: none;
  border: 2px solid #FEBF81;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.nav-link {
  text-decoration: none;
  color: black;
  border: 2px solid #FFA3A5;
  padding: 5px;
  transition: all 0.5s ease;
}

.nav-link:hover {
  background-color: #FFA3A5;
  color: white;
}

.nav-link:hover .nav-item {
  border: 2px solid #FFDC5E;
}

.button-link {
  text-decoration: none;
  color: black;
}

.about-button, .contact-button, .projects-button, .crew-button, .oberman-button, .salama-button {
  width: 225px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: transparent;
  font-size: 2vh;
  text-align: center;
}

.about-button {
  border: 5px solid #FFA3A5;
  transition: all 0.5s ease;
}

.about-button:hover {
  background-color: #FFA3A5;
  color: white;
}

.projects-button {
  border: 5px solid #FEBF81;
  transition: all 0.5s ease;
}

.projects-button:hover {
  background-color: #FEBF81;
  color: white;
}

.contact-button {
  border: 5px solid #FF69EB;
  transition: all 0.5s ease;
}

.contact-button:hover {
  background-color: #FF69EB;
  color: white;
}

.peach {
  color: #FEBF81;
}

.coral {
  color: #FFA3A5;
}

.pink {
  color: #FF86C8;
}

.magenta {
  color: #FF69EB;
}

@media screen and (max-width: 1024px) {

  .about-button, .contact-button, .projects-button, .crew-button, .oberman-button, .salama-button {
    width: 300px;
  }

  .button-link {
    display: inline-block;
  }

  .app-nav {
    width: 100%;
    margin-left: 0;
  }
}

@media screen and (max-width: 375px) {
  .about-button, .contact-button, .projects-button, .crew-button, .oberman-button, .salama-button {
    width: 200px;
  }

  .nav-list {
    padding: 0;
  }
}
.home {
    display: grid;
    grid-template-columns: 40vw 10vw 50vw;
    grid-template-rows: 35vh 25vh 35vh 35vh 35vh 10vh 35vh;
}

.about-box {
    grid-column: 1;
    grid-row: 1;
    margin-left: 10vw;
    margin-top: 1vh;
}

.about-box h1 {
    font-size: 5vh;
    margin-bottom: 0;
}

.about-box p {
    font-size: 2vh;
}

.trh {
    grid-column: 2;
    grid-row: 1;
    position: relative;
}

.pink-line-trh {
    position: absolute;
    left: 25vw;
    background-color: #FF86C8;
    height: 80vh;
    width: 3px;
    z-index: -1;
}

.peach-line-trh {
    position: absolute;
    left: 40vw;
    background-color: #FFBF81;
    height: 65vh;
    width: 3px;
    z-index: -1;
}

.yellow-line-trh {
    position: absolute;
    left: 10vw;
    background-color: #FFDC5E;
    height: 65vh;
    width: 3px;
    z-index: -1;
}

.magenta-line-trh {
    position: absolute;
    left: 55vw;
    background-color: #FF69EB;
    height: 80vh;
    width: 3px;
    z-index: -1;
}

img {
    height: 27vh;
    width: 20vw;
    margin-left: 5vw;
}

.bio-pic {
    grid-column: 3;
    grid-row: 1 / span 2;
    height: 35vh;
