    grid-row: 4;
}

.clh {
    grid-column: 2;
    grid-row: 4;
    position: relative;
}

.peach-line-clh {
    position: absolute;
    top: -50vh;
    left: -25vw;
    background-color: #FFBF81;
    height: 130vh;
    width: 3px;
    z-index: -1;
}

.coral-line-clh {
    position: absolute;
    top: -50vh;
