    text-align: right;
}

.work-box {
    grid-column: 3;
    grid-row: 4;
    margin-left: 10vw;
}

.work-box h1 {
    font-size: 5vh;
    margin-bottom: 0;
}

.work-box p {
    font-size: 2vh;
}

.contact-box {
    grid-column: 1;
    grid-row: 7;
    margin-left: 10vw;
    margin-top: 7vh;
}

.contact-box h1 {
    font-size: 5vh;
    margin-bottom: 0;
}

.contact-box p {
    font-size: 2vh;
}

.brh {
    grid-column: 3;
    grid-row: 7;
    position: relative;
}

.pink-line-brh {
    position: absolute;
    left: 15vw;
    bottom: -45vh;
    background-color: #FF86C8;
    height: 75vh;
    width: 3px;
    z-index: -1;
}

.peach-line-brh {
    position: absolute;
    left: 30vw;
    bottom: -45vh;
    background-color: #FFBF81;
    height: 90vh;
    width: 3px;
    z-index: -1;
}

.yellow-line-brh {
    position: absolute;
    left: 0;
    bottom: -45vh;
    background-color: #FFDC5E;
    height: 90vh;
    width: 3px;
    z-index: -1;
}

.magenta-line-brh {
    position: absolute;
    left: 45vw;
    bottom: -45vh;
    background-color: #FF69EB;
    height: 75vh;
    width: 3px;
    z-index: -1;
}

@media screen and (max-width: 1024px) {
    .home {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
  
    .filler-img-one, .filler-img-two, .filler-img-three, .peach-line-trh, .pink-line-brh, .peach-line-brh, .magenta-line-trh {
      display: none;
    }

  .bio-pic {
    height: auto;
    width: 60vw;
    margin-top: 5vh;
  }

  .work-box, .contact-box {
    margin-top: 5vh;
  }

  .pink-line-trh {
    top: 5vh;
  }

  .yellow-line-trh {
    top: 0vh;
    left: 40vw;
  }

  .coral-line-clh {
    top: -50vh;
    left: -40vw;
  }

  .peach-line-clh {
    top: -40vh;
    left: -25vw;
  }

  .yellow-line-brh {
