    }

    .yellow-line-trh {
        top: 12vh;
        left: 20vw;
        height: 80vh;
    }

    .pink-line-trh {
        top: 0vh;
        left: 40vw;
    }

    .coral-line-clh {
        top: -45vh;
        left: -15vh;
    }

    .peach-line-clh {
        display: none;
    }
}
